f,yBAAyB;AAC3B;AACA;EACE,WAAW;EACX,YAAY;EACZ,gBAAgB;AAClB;AACA;EACE,kBAAkB;EAClB,SAAS;EACT,WAAW;EACX,WAAW;AACb;;AAEA;;;;EAIE,mBAAmB;AACrB;AACA;EACE,YAAY;EACZ,yBAAyB;EACzB,WAAW;EACX,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,yBAAyB;EACzB,mBAAmB;EACnB,eAAe;EACf,kBAAkB;EAClB,UAAU;EACV,kCAAkC;AACpC;;AAEA;EACE,yBAAyB;EACzB,gBAAgB;AAClB","sourcesContent":["* {\r\n    padding: 0;\r\n    margin: 0;\r\n  }\r\n   \r\n  body {\r\n    font-family: 'Poppins', sans-serif;\r\n    background-color: white;\r\n    min-height: 100vh;\r\n    display: flex;\r\n    flex-direction: column;\r\n  }\r\n   \r\n   /* Preloader  */\r\n   #preloader {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    z-index: 9999;\r\n    overflow: hidden;\r\n    background: #fff;\r\n  }\r\n  \r\n  #preloader:before {\r\n    content: \"\";\r\n    position: fixed;\r\n    top: calc(50% - 30px);\r\n    left: calc(50% - 30px);\r\n    border: 6px solid #183d3d;\r\n    border-top-color: #fff;\r\n    border-bottom-color: #fff;\r\n    border-radius: 50%;\r\n    width: 60px;\r\n    height: 60px;\r\n    -webkit-animation: animate-preloader 0.55s linear infinite;\r\n    animation: animate-preloader 0.55s linear infinite;\r\n  }\r\n\r\n  .skip-link {\r\n    position: absolute;\r\n    top: -40px;\r\n    left: 0;\r\n    background-color: #183d3d;\r\n    color: white;\r\n    padding: 8px;\r\n    z-index: 100;\r\n  }\r\n   \r\n  .skip-link:focus {\r\n     top: 0;\r\n  }\r\n  \r\n  @-webkit-keyframes animate-preloader {\r\n    0% {\r\n      transform: rotate(0deg);\r\n    }\r\n  \r\n    100% {\r\n      transform: rotate(360deg);\r\n    }\r\n  }\r\n  \r\n  @keyframes animate-preloader {\r\n    0% {\r\n      transform: rotate(0deg);\r\n    }\r\n  \r\n    100% {\r\n      transform: rotate(360deg);\r\n    }\r\n  }\r\n\r\n\r\n  /*\r\n    AppBar\r\n  */\r\n   \r\n  .app-bar {\r\n    padding: 8px 16px;\r\n    background-color: white;\r\n    display: grid;\r\n    grid-template-columns: auto 1fr auto;\r\n    gap: 10px;\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 99;\r\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n  }\r\n   \r\n  .app-bar .app-bar__menu {\r\n    display: flex;\r\n    align-items: center;\r\n  }\r\n   \r\n  .app-bar .app-bar__menu button {\r\n    background-color: transparent;\r\n    border: none;\r\n    font-size: 18px;\r\n    padding: 14px;\r\n    cursor: pointer;\r\n  }\r\n   \r\n  .app-bar .app-bar__brand {\r\n    display: flex;\r\n    align-items: center;\r\n  }\r\n   \r\n  .app-bar .app-bar__brand h1 {\r\n    color: #183d3d;\r\n    text-transform: uppercase;\r\n    font-size: 22px;\r\n    user-select: none;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation {\r\n    position: absolute;\r\n    margin-bottom: 0;\r\n    top: 50px;\r\n    left: -180px;\r\n    width: 150px;\r\n    transition: all 0.3s;\r\n    padding: 8px;\r\n    background-color: white;\r\n    overflow: hidden;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation.open {\r\n    left: 0;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    color: black;\r\n    padding: 10px;\r\n    width: 100%;\r\n    font-weight: 400;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover {\r\n    transform: scale(1.5);\r\n    transition: 0.1s;\r\n    font-weight: 600;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:focus {\r\n    transform: scale(1.3);\r\n    transition: 0.1s;\r\n    font-weight: 550;\r\n  }\r\n\r\n  /* hero */\r\n\r\n.hero {\r\n  display: flex;\r\n  align-items: center;\r\n  min-height: 190px;\r\n  text-align: center;\r\n  background-image: url(\"../public/image/hero/hero-image.jpg\");\r\n  background-position: center;\r\n  background-color: green;\r\n}\r\n\r\n.hero-inner {\r\n  margin: 0 auto;\r\n}\r\n\r\n.hero-inner p{\r\n  text-align: center;\r\n  color: #fff;\r\n  font-size: 1em;\r\n}\r\n\r\n.hero-title {\r\n  /* font-family: \"Luckiest Guy\", cursive; */\r\n  color: #fff;\r\n  font-size: 3em;\r\n  letter-spacing: 5px;\r\n}\r\n   \r\n  /*\r\n    Main Content\r\n  */\r\n   \r\n  main {\r\n    padding: 32px;\r\n    flex: 1;\r\n  }\r\n   \r\n  .content {\r\n    margin: 0 auto;\r\n    min-height: 100%;\r\n  }\r\n   \r\n  .content .content__heading {\r\n    font-weight: normal;\r\n  }\r\n   \r\n   \r\n  /*\r\n    restaurants\r\n  */\r\n   \r\n  .restaurants {\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n    margin: 32px 0;\r\n  }\r\n   \r\n   \r\n  /*\r\n    restaurant Item\r\n  */\r\n   \r\n  .restaurant-item {\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    border-radius: 8px;\r\n    overflow: hidden;\r\n  }\r\n   \r\n  .restaurant-item__header {\r\n    position: relative;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__header .restaurant-item__header__poster {\r\n    height: 200pt;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n    position: absolute;\r\n    padding: 8px;\r\n    bottom: 20px;\r\n    right: 0;\r\n    display: inline-block;\r\n    background-color: rgba(0, 0, 0, 0.650);\r\n    color: white;\r\n  }\r\n\r\n  .restaurant-item .restaurant-item__header .restaurant-item__header__city {\r\n    position: absolute;\r\n    padding: 8px;\r\n    top: 20px;\r\n    left: 0;\r\n    display: inline-block;\r\n    background-color: rgba(0, 0, 0, 0.650);\r\n    color: white;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n    margin-left: 10px;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__content {\r\n    padding: 16px;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__content h3 {\r\n    margin: 0 0 10px 0;\r\n    text-align: center;\r\n  }\r\n\r\n  .restaurant-item .restaurant-item__content h3:hover {\r\n    transform: scale(1.5);\r\n    transition: 0.1s;\r\n  }\r\n   \r\n  .restaurant-item .restaurant-item__content h3 a {\r\n    /* color: #db0000; */\r\n    color: #183d3d;\r\n    text-decoration: none;\r\n    padding: 22px;\r\n  }\r\n\r\n  .restaurant-item .restaurant-item__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 4; /* number of lines to show */\r\n    -webkit-box-orient: vertical;\r\n  }\r\n   \r\n  /*\r\n    restaurant\r\n  */\r\n  .restaurant {\r\n    width: 100%;\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 18px 16px;\r\n  }\r\n\r\n  .restaurant .restaurant__poster {\r\n    width: 100%;\r\n    max-width: 445px;\r\n  }\r\n   \r\n  .restaurant .restaurant__info h4 {\r\n    margin: 8px 0;\r\n  }\r\n\r\n/* detail */\r\n\r\n.block-text{\r\n  padding: 1px 8px;\r\n  background-color: #183d3d;\r\n  color: #fff;\r\n  display: inline-block;\r\n}\r\n\r\n.detail-container{\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  }\r\n\r\n  .underlined{\r\n  text-decoration: underline;\r\n  text-decoration-color: rgba(0, 0, 0, 0.622);\r\n  }\r\n\r\n  .detail-title{\r\n    padding: 30px 0 0 0;\r\n  }\r\n\r\n  .detail-date{\r\n    color: rgba(0, 0, 0, 0.622);\r\n    font-size: 12px;\r\n  }\r\n\r\n  .detail-review h4{\r\n    margin-top: 17px;\r\n  }\r\n\r\n  .detail-review hr{\r\n    margin-top: 4px;\r\n  }\r\n  \r\n  /* table */\r\n\r\n  table {\r\n    font-family: \"Arial\";\r\n \r\n}\r\n.table {\r\n    border-collapse: collapse;\r\n    margin-top: 9px;\r\n}\r\n.table th, \r\n.table td {\r\n    border-bottom: 1px solid #cccccc;\r\n    border-left: 1px solid #cccccc;\r\n    padding: 9px 21px;\r\n}\r\n.table th, \r\n.table td:last-child {\r\n    border-right: 1px solid #cccccc;\r\n}\r\n.table td:first-child {\r\n    border-top: 1px solid #cccccc;\r\n}\r\n \r\n/* Table Header */\r\n.table thead th {\r\n    background-color: #183d3d;\r\n    color: #FFFFFF;\r\n}\r\n \r\n/* Table Body */\r\n.table tbody td {\r\n    color: #353535;\r\n}\r\n.table tbody tr:nth-child(odd) td {\r\n    background-color: #f5fff9;\r\n}\r\n.table tbody tr:hover th,\r\n.table tbody tr:hover td {\r\n    background-color: #f0f0f0;\r\n    transition: all .2s;\r\n}\r\n \r\n/*Tabel Responsive 1*/\r\n.table-container {\r\n    overflow: auto;\r\n}\r\n\r\n  /*\r\n    Like\r\n  */\r\n  .like {\r\n    width: 55px;\r\n    height: 55px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background-color: #183d3d;\r\n    \r\n    position: fixed;\r\n    bottom: 16px;\r\n    right: 16px;\r\n    border-radius: 50%;\r\n    border: 0;\r\n    \r\n    font-size: 18px;\r\n    color: white;\r\n    cursor: pointer;\r\n  }\r\n   \r\n  /*\r\n    Footer\r\n  */\r\n   \r\n  footer {\r\n    text-align: center;\r\n    color: #fff;\r\n    background-color: #000;\r\n    padding: 1em;\r\n    bottom: 0;\r\n    font-weight: 200;\r\n  }\r\n   \r\n\r\n  footer p {\r\n    text-align: center;\r\n    color: #fff;\r\n  }\r\n   \r\n\r\n  /*\r\n  Button\r\n*/\r\n \r\n.btn {\r\n  display: inline-block;\r\n  padding: 12px 24px;\r\n  background-color: #DB0000;\r\n  border: 1px solid #770000;\r\n  border-radius: 4px;\r\n \r\n  font-weight: 500;\r\n  font-size: 16px;\r\n  color: #ffffff;\r\n \r\n  cursor: pointer;\r\n  transition: .15s ease-in-out;\r\n}\r\n \r\n.btn:hover {\r\n  background-color: #DB0000BB;\r\n}\r\n.btn:active {\r\n  background-color: #DB0000DD;\r\n}\r\n.btn:focus-visible {\r\n  outline: 3px solid #DB000055;\r\n}\r\n\r\n/* FORM */\r\n@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');\r\n\r\n.text{\r\n  text-align: center;\r\n  font-size: 41px;\r\n  font-weight: 600;\r\n  font-family: 'Poppins', sans-serif;\r\n}\r\n\r\n form{\r\n  width: 105%;\r\n  margin: 0 0 0 -20px;\r\n}\r\n\r\n form h3{\r\n  margin-left: 20px;\r\n }\r\n\r\n \r\nform .form-row{\r\n  display: flex;\r\n  margin: 32px 0;\r\n}\r\nform .form-row .input-data{\r\n  width: 100%;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  margin: 0 20px;\r\n  position: relat